body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

.search-wrapper {
  position: relative;
  margin: 0 auto;
  max-width: 1170px;
  background-color: #fff; }
  .search-wrapper input[type='text'] {
    padding: 10px 15px;
    width: 100%;
    border: none; }
  .search-wrapper input[type='text']:focus {
    outline: none; }

header {
  position: -webkit-sticky;
  position: -moz-sticky;
  position: -ms-sticky;
  position: -o-sticky;
  position: sticky;
  margin-bottom: 10px;
  top: 0px;
  z-index: 99; }

.header {
  position: relative;
  top: 60px; }
  .header-top-line {
    min-height: 25px;
    background-color: #6a0080; }
  .header-line {
    padding: 15px 0px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    -webkit-align-items: center;
            align-items: center;
    min-height: 80px;
    background: linear-gradient(-45deg, #d03def, #e43fb2, #23A6D5, #5e35b1);
    background-size: 400% 400%;
    -webkit-animation: Gradient 30s ease infinite;
    animation: Gradient 30s ease infinite; }
  .header-bootom-line {
    top: 0;
    left: 0;
    position: relative;
    z-index: -1;
    background-color: #fff;
    box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.3);
    transition: all 0.5s ease-out; }
    .header-bootom-line.hide {
      top: -36px;
      left: 0;
      overflow: hidden;
      transition: all 0.5s ease-in; }
  .header h1 {
    font-size: 25px; }

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

.short-card-wrapper {
  margin-bottom: 25px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  text-align: left;
  background: #fff;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1); }
  @media only screen and (min-width: 481px) and (max-width: 768px) {
    .short-card-wrapper {
      max-height: 278px; } }
  @media only screen and (min-width: 769px) {
    .short-card-wrapper {
      width: 70%;
      max-height: 278px;
      margin-left: 40px; } }

.short-card__img {
  width: 50%;
  max-width: 200px; }
  .short-card__img img {
    width: 100%;
    height: 100%;
    min-height: 250px;
    min-width: 200px; }

.short-card__info {
  padding: 0 20px;
  position: relative; }

.short-card__title {
  padding: 20px 0; }

.short-card__description {
  text-align: justify;
  font-size: 14px;
  display: block;
  max-height: 135px;
  overflow: hidden; }
  @media only screen and (max-width: 480px) {
    .short-card__description {
      display: none; } }

.short-card__controls {
  padding-top: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }
  @media only screen and (max-width: 480px) {
    .short-card__controls {
      padding-top: 0;
      padding-bottom: 20px; } }

.short-card__link {
  padding: 5px 10px;
  cursor: pointer;
  border: 1px solid #F5F5F6;
  background-color: #d05ce3;
  color: #fff;
  text-decoration: none;
  text-transform: capitalize;
  font-size: 11px;
  font-weight: 700;
  outline: none; }
  .short-card__link--margin {
    margin-right: 25px; }

.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.4); }
  .modal .modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 55%; }
    .modal .modal-content textarea {
      width: 100%;
      height: 200px;
      padding: 10px 10px;
      box-sizing: border-box;
      border: 1px solid #ccc;
      border-radius: 4px;
      background-color: #f8f8f8;
      resize: none;
      outline: none; }
    .modal .modal-content input {
      width: 100%;
      height: 30px;
      padding: 10px 10px;
      box-sizing: border-box;
      border: 1px solid #ccc;
      border-radius: 4px;
      background-color: #f8f8f8;
      outline: none; }
  .modal .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold; }
  .modal .close:hover,
  .modal .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer; }

$color-primary: #5e35b1;
$color-primary-dark: #6a0080;
$color-primary-light: #d05ce3;
$color-grey-dark: #E1E2E1;
$color-grey-light: #F5F5F6;

$text-color-primary: #1a1a1a;
$text-color-button: #fff;

$small: 480px;
$large: 769px; 

$container-max-width: 1170px;
@mixin respond-to($media) {
    @if $media == mobile {
      @media only screen and (max-width: $small) { @content; }
    }
    @else if $media == tablet {
      @media only screen and (min-width: $small + 1) and (max-width: $large - 1) { @content; }
    }
    @else if $media == desktop {
      @media only screen and (min-width: $large) { @content; }
    }
  }

  //animations

  @-webkit-keyframes Gradient {
    0% {
      background-position: 0% 50%
    }
    50% {
      background-position: 100% 50%
    }
    100% {
      background-position: 0% 50%
    }
  }
  
  @-webkit-keyframes Gradient {
    0% {
      background-position: 0% 50%
    }
    50% {
      background-position: 100% 50%
    }
    100% {
      background-position: 0% 50%
    }
  }
  
  @keyframes Gradient {
    0% {
      background-position: 0% 50%
    }
    50% {
      background-position: 100% 50%
    }
    100% {
      background-position: 0% 50%
    }
  }
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: $color-grey-light;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $text-color-primary;

  .main {
    padding: 0 10px;
    @include respond-to(desktop){
      padding: 0;
    }
  }

}
